<!-- content_list.vue -->
<template>
	<div class="row">
        <div class="col-md-12">
          <!-- The time line -->
          <ul class="timeline">
            <!-- timeline time label -->
            <li class="time-label">
                  <span class="bg-green">
                    {{date}}
                  </span>
            </li>
            <!-- /.timeline-label -->
            <!-- timeline item -->
            <li v-for="item in items">
              <i class="fa fa-book bg-blue"></i>

              <div class="timeline-item">
                <span class="time"><i class="fa fa-clock-o"></i> {{item.time}}</span>

                <h3 class="timeline-header">
                  <router-link :to="{name:'article',params:{fileName:item.fileName} }">
                    {{item.text}}
                  </router-link>
                </h3>

                <div v-if="item.dec" class="timeline-body">
                  {{item.dec}}
                </div>
                <!-- <div class="timeline-footer">
                  <i class="fa fa-tag text-aqua"></i>
                  <small class="label bg-navy">http</small>
                  <small class="label bg-navy">http</small>
                </div> -->
              </div>
            </li>
            <!-- END timeline item -->
            <li>
              <i class="fa fa-clock-o bg-gray"></i>
            </li>
          </ul>
        </div>
        <!-- /.col -->
      </div>
</template>
<script>
export default {
  name: 'content_list',
  data(){
  	return {
  		date:""
  	}
  },
  props: ['items'],
  mounted(){
      var date = new Date();
      this.date = date.toLocaleDateString()
    }
}
</script>


