<!-- http.vue -->
<template>
	<div class="content-wrapper">
		<content-header v-bind:menuList="menuList"></content-header>
		<section class="content">
		  <h4>Http</h4>
    	
      <content-list :items="items"></content-list>
    </section>
	</div>
</template>

<script>
  import contentHeader from './content_header'
  import contentList from './content_list'
  export default{
    data(){
      return {
        menuList:[
          {
            text:"Http",
            isActive:"true",
            url:"http",
          }
        ],
        items:[
          {
            fileName:"$get_file",
            text:"$.get()markdown",
            time:"2016-11-14",
            dec:"markdown"
          }
        ],
      }
    },
    components:{
      contentHeader,
      contentList
    },
    // methods:{
    //   ifTime(item,index,items){
    //     if(index>1){
    //       if(item.time==items[index-1].time){
    //         return false;
    //       }
    //     }
    //     return true
    //   }
    // }
  }
</script>



